#home-news article.flickity-cell {
  width: 640px;
  max-width: 100%;
  margin-right: 24px;
}

.mega-wpml-ls-item-en a::before {
  content: "EN";
  font-family: var(--elixir-body-font-family);
}

.mega-wpml-ls-item-sr a::before {
  content: "SR";
  font-family: var(--elixir-body-font-family);
}

.elixir-tabs .nav-link {
  position: relative;
  border-bottom: 1px solid #EBECEB;
}
.elixir-tabs .nav-link.active {
  font-weight: 700;
  color: #3A683D;
}
.elixir-tabs .nav-link.active::before {
  content: "";
  display: block;
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 2px;
  background: #819825;
}

.pointer {
  cursor: pointer;
}

.otgs-development-site-front-end {
  display: none !important;
}

.bg-elixir-gradient {
  background: rgb(198, 212, 32);
  background: linear-gradient(-45deg, rgb(198, 212, 32) 0%, rgb(186, 207, 47) 40%, rgb(129, 152, 37) 100%);
}

.hero-image,
.entry-cover {
  height: 350px;
}

.hero-image {
  padding-left: 15px;
}

.elixir-video-icon {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 64px;
  height: 64px;
}

.cat-links a {
  font-weight: 600;
  color: #819825;
}

.post-author,
.cat-links,
.posted-on,
.font-heading {
  font-family: "Montserrat";
}

.page-header {
  padding-top: 40px;
  padding-bottom: 40px;
}

.elixir-news-slider,
.elixir-image-slider {
  transition: opacity 0.6s ease;
}
.elixir-news-slider:not(.flickity-enabled),
.elixir-image-slider:not(.flickity-enabled) {
  display: flex;
  opacity: 0;
}
.elixir-news-slider.flickity-enabled,
.elixir-image-slider.flickity-enabled {
  opacity: 1;
}
.elixir-news-slider .flickity-page-dots,
.elixir-image-slider .flickity-page-dots {
  justify-content: center;
}
.elixir-news-slider .article-content,
.elixir-image-slider .article-content {
  background: linear-gradient(180deg, transparent 0, rgba(0, 0, 0, 0.3) 60%);
}

.ratio-elixir-news {
  --elixir-aspect-ratio: 150%;
}
@media (min-width: 768px) {
  .ratio-elixir-news {
    --elixir-aspect-ratio: 56.25%;
  }
}

.alumofluor-logistics-map {
  margin-top: -50%;
}

.backdrop-blur {
  backdrop-filter: blur(10px);
}

@media (min-width: 768px) {
  .hero-image,
.entry-cover {
    height: 450px;
  }

  .elixir-video-icon {
    width: 128px;
    height: 128px;
  }

  .alumofluor-logistics-map {
    position: absolute;
    width: 80%;
    height: auto;
    bottom: -25%;
    right: 0;
    margin-top: 0;
  }
}
@media (min-width: 992px) {
  .hero-image,
.entry-cover {
    height: 500px;
  }

  .elixir-video-icon {
    width: 164px;
    height: 164px;
  }

  nav.flex-lg-column .nav-link {
    border-bottom: none;
  }
  nav.flex-lg-column .nav-link.active::before {
    display: none;
  }
}
@media (min-width: 1200px) {
  .entry-cover {
    height: 555px;
  }

  .hero-image {
    height: 630px;
  }

  /*.elixir-video-icon {
  	width: 214px;
  	height: 214px;
  }*/
  .alumofluor-logistics-map {
    width: 70%;
    max-width: 1400px;
  }
}
